import {Canvas} from "utils/canvas/Canvas";
import {Painter, LineStyle} from "utils/canvas/Painter";
import {Vector,V} from "Vector";

declare const Module: any;

const MAX_PATHS = 1000;

const W = 500;
const H = 500;

const L = 5;

const colors = ["#ffee58", "#039be5", "#ffb74d"];

class Obj {
    public pos: Vector;
    public mass: number;

    public constructor(pos: Vector, mass: number) {
        this.pos = pos;
        this.mass = mass;
    }
}

function drawTail(tail: Vector[], color: string, p: Painter): void {
    if (tail.length <= 1)
        return;

    p.setLineStyle(new LineStyle(color, 4, "round"));
    for (let l = 1; l < tail.length; l++) {
        p.setAlpha((l - 1) / tail.length);
        p.drawLine(tail[l-1], tail[l]);
    }
    p.setAlpha(1.0);
}

function getPlanets(): Obj[] {
    const planets = [];

    const numObjs = Module._num_objs();
    for (let i = 0; i < numObjs; i++) {
        const x = Module._get_obj_x(i);
        const y = Module._get_obj_y(i);
        const m = Module._get_obj_mass(i);

        planets.push(new Obj(V(x, y), m));
    }

    return planets;
}

Module.onRuntimeInitialized = function() {
    const canvas = new Canvas("canvas", () => {});

    const tails = [[], [], []];

    Module._init(3);
    function step() {
        for (let i = 0; i < 200; i++)
            // Module._step(1);
            Module._step(0.05);

        const planets = getPlanets();
        canvas.draw((p) => {
            // Draw tails first so they appear behind planets
            tails.forEach((tail, i) => drawTail(tail, colors[i], p));

            planets.forEach((planet, i) => {
                // Draw planet circle
                p.setFillColor(colors[i]);
                p.fillCirc(planet.pos, Math.log10(planet.mass)*5+1);

                // Add to tail
                tails[i].push(V(planet.pos));
                if (tails[i].length > MAX_PATHS)
                    tails[i].splice(0, tails[i].length - MAX_PATHS);
            });
        });
        requestAnimationFrame(step);
    }
    requestAnimationFrame(step);
}